<script type="text/javascript" src="lotteryStation.js"></script>
<script type="text/javascript" src="https://maps.googleapis.com/maps/api/js?key=AIzaSyApEA9EftcZTKkjUacMSeOKAkNTrhHcgH4&callback=initMap&v=weekly"></script>
<script>
import Vue from 'vue';
import modeImg from 'C:/Users/LINDA/Desktop/web前端教育訓練/lotterystation/src/assets/mode_1.png';
import modeImg2 from 'C:/Users/LINDA/Desktop/web前端教育訓練/lotterystation/src/assets/mode_2.png'
import markerIcon from 'C:/Users/LINDA/Desktop/web前端教育訓練/lotterystation/src/assets/position_center.png'
//import google from 'google'
import * as VueGoogleMaps from 'vue2-google-maps';
import axios from "axios";

Vue.use(VueGoogleMaps,{
  load:{
    key:"AIzaSyApEA9EftcZTKkjUacMSeOKAkNTrhHcgH4",
    //libraries:"places"
  },
  installComponents: true
});

export default {
  name:"AddGoogleMap",
  data(){
    return{
      //google: null,
      marker: {position:{lat: 25.084987946164247, lng: 121.57745364602187}},
      center: {lat: 25.0810263244162, lng: 121.57520030000002},
      options: {
        mapTypeControl: false, // 是否能切換地圖樣式
        scaleControl: false, // 是否顯示地圖比例尺
        streetViewControl: false, // 是否能使用街景功能
        fullscreenControl: false, // 是否開啟全螢幕地圖功能
        disableDefaultUI: true,
        scrollwheel: true, // 是否支援滑鼠滾輪
        clickableIcons: false // 地圖標記是否可點擊
      },
      selectedDist: 1,
      modeImg: modeImg,
      mapMode: true,
      markerIcon: markerIcon,
      stationInfo: [],
      stationName: "",
      stationLat: "",
      stationLng: "",
      stationAddress: "",
      stationDist: "",
      firstPosition: false,
      startX:0,
      endX:0,
      slideStyle:{
        left:0,
        transition: "none"
      }
    };
  },
  mounted(){
    this.initMap();
  },
  // 定義方法
  methods:{
    // 初始化地圖
    initMap(){
      // 取得定位
      if (navigator.geolocation){
        navigator.geolocation.getCurrentPosition(nowPosition => {
          this.center.lat = parseFloat(nowPosition.coords.latitude);
          this.center.lng = parseFloat(nowPosition.coords.longitude);
        }, err => {
          //alert("取得當前位置失敗");
        });
      }
      //alert("當前位置: " + this.center.lat + "," + this.center.lng);
      this.getLotteryStationPosition();
    },
    // 取得投注站資訊
    getLotteryStationPosition(){
      let headers = {
      'Content-Type': 'application/json;charset=utf-8',
      'Encrypt': "0"
      }
      let searchData = {
        "lat": this.center.lat,
        "lon": this.center.lng,
        "distance": 1
      }
      axios.post("https://smuat.megatime.com.tw/taiwanlottery/api/Home/Station",
        searchData,{headers})
      .then((res) => {
        console.log(res.data);
        //alert(res.data.content.list);
        //alert("中心位置" + this.center.lat);
        this.stationInfo = res.data.content.list;
        //alert(this.stationInfo);
        //this.stationName = this.stationInfo[0].name;
      })
      .catch((err) =>{
        console.log(err);
        alert("取得投注站資訊失敗");
        alert(err);
      }) 
    },
    // 更改選擇模式
    changeMode() {
      //alert("changeMode");
      if (this.modeImg === modeImg) {
        this.modeImg = modeImg2;
        this.mapMode = false;
      } else {
        this.modeImg = modeImg;
        this.mapMode = true;
      }  
    },
    // 更改選擇距離
    changeDistance(e){
      //alert("changeDist");
      this.selectedDist = e.target.value;
      //alert(e.target.value);
      // 串接投注站資訊
      let headers = {
        'Content-Type': 'application/json;charset=utf-8',
        'Encrypt': "0"
      }
      let searchData = {
        "lat": 25.0810263244162,
        "lon": 121.57520030000002,
        "distance": parseInt(this.selectedDist)
      }
      axios.post("https://smuat.megatime.com.tw/taiwanlottery/api/Home/Station",
        searchData,{headers})
      .then((res) => {
        console.log(res.data);
        //alert(res.data.content.list);
        //alert(this.selectedDist);
        this.stationInfo = res.data.content.list;
      })
      .catch((err) =>{
        console.log(err);
        alert("串接失敗");
        alert(err);
      })
    },
    // 導航
    getDirection(){
      
    },
    // 投注站資訊移動
    
  }
}

</script>

<template>
<body v-bind:class="{'mapMode': mapMode}">
  <div id="app">
      <div v-bind:class="{'titleAllMapMode': mapMode, 'titleAllListMode': !mapMode}">
        <div class="title">附近投注站</div>
        <div v-bind:class="{'modeMapMode': mapMode, 'modeListMode': !mapMode}" id="mode" v-on:click="changeMode" :style="{backgroundImage: 'url(' + modeImg + ')'}">
        </div>
      </div>
      <select v-model="selectedDist" class="selectDist" id="selectDist" @change="changeDistance($event)" v-if="mapMode">
        <option class="dist" value=1>1公里</option>
        <option class="dist" value=2>2公里</option>
        <option class="dist" value=5>5公里</option>
      </select>
    <!--投注站資訊-->
      <ul id="lotteryInfoAll" v-bind:class="{'lotteryInfoAllMapMode': mapMode, 'lotteryInfoAllListMode': !mapMode}">
        <li v-bind:class="{'lotteryInfoMapMode': mapMode, 'lotteryInfoListMode': !mapMode}" v-for="(station, index) in stationInfo" :key="index">
          <table v-bind:class="{'lotteryInfoDetailMapMode': mapMode, 'lotteryInfoDetailListMode': !mapMode}">
          <tr>
          <td class="name">{{station.name}}</td>
          <td class="locationDist">{{station.distance}}公里</td>
          </tr>
          <tr>
          <td class="address">{{station.address}}</td>
          <td class="navigation"><button class="navigationBtn" @click="getDirection">導航</button></td>
          </tr>
        </table>
        </li>
      </ul>
    <!--地圖-->
    <!--div id="map">
    </div-->
    <GmapMap
      :center="center"
      :zoom="16"
      map-type-id="roadmap"
      :options="options"
      style="width:100%; height: 650px; margin-top: 24px;" v-if="mapMode">
      <GmapMarker v-for="(station, index) in stationInfo" :key="index"
        :icon='{
          url:require("C:/Users/LINDA/Desktop/web前端教育訓練/lotterystation/src/assets/position.png"),
          scaledSize:{width:40, height: 58}
          }'
        :position="{lat: parseFloat(station.lat), lng: parseFloat(station.lon)}"
        :clickable="false"
        :draggable="false"/>
    </GmapMap>
  </div>
</body>  
</template>

<style>
  html,
  body {
    width: 100%;
    height: 100%; 
    padding: 0;
    margin: 0;
  }
  .mapMode{
    width: 100%;
    height: 100%; 
    padding: 0;
    margin: 0;
    overflow: hidden;
  }
  .titleAllMapMode{
    position: relative;
    z-index: 40;
    height: 30px;
    width: 100%;
    top: 10px;
    margin-bottom: 10px;
    /*border: 1px solid red;*/
  }
  .modeMapMode{
    position: relative;
    height: 37px;
    width: 85px;
    margin-top: -2px;
    margin-right: 10px;
    float: right;
    background-size: 100% 100%;
    background-repeat:no-repeat;
    box-shadow: 1px 1px 3px 2px rgba(0.1, 0.1, 0.1, 0.1);
    cursor: pointer;
  }
  .titleAllListMode{
    position: fixed;
    z-index: 40;
    height: 58px;
    width: 100%;
    line-height: 58px; /*行高(垂直置中)*/
    top: 0px;
    margin-bottom: 10px;
    background: white;
  }
  .modeListMode{
    position: relative;
    height: 37px;
    width: 85px;
    margin-top: 10px;
    margin-right: 10px;
    float: right;
    background-size: 100% 100%;
    background-repeat:no-repeat;
    box-shadow: 1px 1px 3px 2px rgba(0.1, 0.1, 0.1, 0.1);
    cursor: pointer;
  }
  .title{
    position: absolute;
    width: 100%;
    text-align: center;
    font-size: 22px;
    color: rgb(255, 136, 0);
    /*border: 1px solid red;*/
    background: none;
  }
/*#map {
    height: 600px;
    width: 98%;
    z-index: 0;
}*/

/*距離選擇*/
.selectDist{
  position: absolute;
  top: 10%;
  right: 3%;
  z-index: 20;
  width: 18%;
  height: 5%;
  display: inline-block;
  /*border: 1px solid orangered;*/
  border-radius: 5px;
  /*下拉箭頭設定*/
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: rgb(255, 69, 1);
  opacity: 0.6;
  font-size: 20px;
  color: white;
}
.dist{
  width: 100%;
  height: 200%;
  border: none;
  background-color: rgb(255, 136, 0);
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
  text-align: center;
}
.selectDist option:hover{
  background-color: orangered;
}
.selectDist:hover{
  display: block;
}
.selectDist:hover .dist{
  background-color: rgb(255, 102, 47);
}

/*投注站資料*/
/*地圖模式*/
.lotteryInfoAllMapMode{
  position: absolute;
  z-index: 20;
  bottom: 5%;
  left: 0;
  width: 90%;
  height: 15%;
  margin-left: 1%;
  white-space: nowrap; /*不會換行*/
  overflow-x: auto; /*可滑動*/
  overflow-y: hidden;
}

/*.lotteryInfoAll::-webkit-scrollbar{ /*可隱藏滾動條*/
  /*display: none;
}*/
.lotteryInfoMapMode{
  width: 90%;
  height: 100%;
  margin-right: 5%;
  display: inline-block;
}
.lotteryInfoDetailMapMode{
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: white;
  border: 1px solid rgb(255, 136, 0);
}
/*清單模式*/
.lotteryInfoAllListMode{
  position: relative;
  z-index: 20;
  width: 90%;
  height: 100%;
  top: 40px;
  left: -5%;
  overflow-y: auto; /*可滑動*/
  overflow-x: hidden;
  /*border: 1px solid red;*/
}
.lotteryInfoListMode{
  width: 100%;
  height: 100%;
  margin-bottom: 2%;
  display: inline-block;
}
.lotteryInfoDetailListMode{
  width: 100%;
  height: 100px;
  border-radius: 10px;
  background-color: white;
  /*border: 1px solid orangered;*/
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.1);
}
.name{
  width: 75%;
  height: 30%;
  padding-left: 3%;
  padding-top: 3%;
  color: rgb(255, 136, 0);
  font-size: 20px;
  /*border: 1px solid red;*/
}
.address{
  width: 75%;
  padding-left: 3%;
  color: balck;
  font-size: 15px;
  word-wrap:break-word;
}
.locationDist{
  width: 25%;
  text-align: right;
  padding-top: 2%;
  color: rgb(255, 136, 0);
  font-size: 15px;
  text-align: center;
}
.navigation{
  position: relative;
  width: 25%;
  padding-right: 4.5%;
}
.navigationBtn{
  width: 80%;
  height: 80%;
  position: absolute; 
  top: 0;             
  bottom: 0;           
  left: 0;        
  right: 0;
  margin: auto;
  background-color: rgb(255, 145, 0);
  color: white;
  font-size: 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

</style>
